*{
    font-family: Helvetica,Arial, sans-serif;
}

body {
    padding: 60px;
}

input{
    font-size: 1.rem;
    width: 100%;
    color: grey;
}
.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    overflow: hidden;
    margin:  0  10px 0 10px;
}

.card p{
    padding: 5px;
    text-align: center;
}
.card img{
    max-height: 170px;
}